import * as React from 'react';
import { useParams } from 'react-router-dom';

import './supplier-product-list.scss';

import Staff from '@miq/staffjs';
import { Button, HorizontalScrollGridLayout, Icons, Pagination, ViewSection } from '@miq/componentjs';

import { StaffProduct } from '../Product';
import { useProductListRequest } from '../Product';
import { ProductListFilterForm, ProductQuickUpdateForm } from '../Product/forms';
import { ProductDeleteButton } from '../Product/components';
import { CategoryChoicesType, ProductType } from '../types';

import { SupplierOrder } from './models';

export type SupplierProductListProps = {
  orderInstance: ReturnType<typeof SupplierOrder>;
  categories: CategoryChoicesType;
  refreshOrder: () => Promise<null | undefined>;
};

export default function SupplierProductList({ orderInstance, refreshOrder, categories }: SupplierProductListProps) {
  const { ordSlug } = useParams();
  const { res = { data: { results: [] } } } = useProductListRequest(ordSlug);
  const [selected, setSelected] = React.useState<string[]>([]);

  return (
    <Staff.Section
      header={<ProductListFilterForm categories={categories} />}
      footer={<Pagination {...res.data} component={Staff.NavLink} to />}
      className="supplier-p-list"
    >
      {res.data?.results.length ? (
        <>
          {selected.length !== 0 && (
            <div className="bg-white" style={{ position: 'sticky', top: 0, zIndex: 1000 }}>
              <Button>Group together</Button>
            </div>
          )}

          {res.data.results.map((item: ProductType) => (
            <SupplierProductListItem
              {...{ orderInstance, categories, selected, setSelected, refreshOrder }}
              instance={StaffProduct(item)}
              key={item.slug}
            />
          ))}
        </>
      ) : null}
    </Staff.Section>
  );
}

type SupplierProductListItemProps = {
  orderInstance: ReturnType<typeof SupplierOrder>;
  categories: CategoryChoicesType;
  instance: ReturnType<typeof StaffProduct>;
  selected: string[];
  setSelected: React.Dispatch<React.SetStateAction<string[]>>;
  refreshOrder: () => Promise<null | undefined>;
};

const SupplierProductListItem = (props: SupplierProductListItemProps) => {
  const [hide, setHide] = React.useState<boolean>(false);
  const { instance, categories } = props;

  if (hide) return null;

  const { supplier_item } = instance;
  const { orderInstance } = props;
  return (
    <ViewSection
      border
      title={
        <div className="d-flex align-items-center">
          <ItemCheckBox {...props} />

          <div style={{ width: 48, height: 48 }}>
            <Staff.Img.Square slug="slug" src="src" {...instance.cover_data} className="rounded" />
          </div>
        </div>
      }
      actions={
        <div className="d-flex">
          <ProductDeleteButton
            instance={instance}
            onSuccess={() => {
              setHide(true);
              props?.refreshOrder?.();
            }}
            className="me-2"
          />
          <Staff.Link to={`./${instance.slug}/`} className="btn btn-primary-3">
            <Icons.PencilSquare />
          </Staff.Link>
        </div>
      }
      className="suppier-p-list-item bg-white"
    >
      <HorizontalScrollGridLayout className="form-tab no-scroll-bar gap-2">
        <ProductQuickUpdateForm instance={instance} categories={categories} orderSlug={orderInstance?.slug} />

        <div className="">
          <div className="mb-1">
            {`${supplier_item.item_id ? <span className="">Item ID: {supplier_item.item_id}</span> : ''}`}
          </div>
          <ul>
            {supplier_item.category && (
              <li className="mb-1">
                <div className="text-sm">Supplier Category</div>
                <div className="text-muted">{supplier_item.category}</div>
              </li>
            )}
            <li className="mb-1">
              <div className="text-sm">FOB Cost({orderInstance.currency})</div>
              <div className="text-muted">{supplier_item.cost}</div>
            </li>

            {supplier_item.url && (
              <li className="">
                <a href={supplier_item.url} target="_blank" className="text-underline" rel="noopener noreferrer">
                  View supplier product page
                </a>
              </li>
            )}
          </ul>
        </div>
      </HorizontalScrollGridLayout>
    </ViewSection>
  );
};

const ItemCheckBox = ({ instance, selected, setSelected }: SupplierProductListItemProps) => (
  <input
    type="checkbox"
    name="selected"
    className="me-2"
    onChange={({ target }) => {
      const { checked } = target;
      if (checked) setSelected([...selected, instance.slug!]);
      else setSelected((selected) => selected.filter((i) => i !== instance.slug));
    }}
  />
);
