import * as React from 'react';
import Form, {
  DescriptionField,
  NameField,
  FormProps,
  IFormCallbackProps,
  IFormUpdateProps,
  useForm,
  TextFieldProps,
  CheckboxFieldProps,
  SelectFieldProps,
  TextChoicesType,
} from '@miq/formjs';
import { SupplierOrder } from './models';

interface ISupplierOrderUpdateFormProps extends IFormUpdateProps {
  instance: ReturnType<typeof SupplierOrder>;
  fields: string[];
}

export const SupplierOrderUpdateForm: React.FC<ISupplierOrderUpdateFormProps> = ({ instance, fields, ...props }) => {
  const { onSuccess, onError, ...rest } = props;
  const { context } = rest;

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const fd: any = {};
    fields.forEach((field) => {
      const value = context.values[field];
      if (value == null) return;
      fd[field] = value;
    });

    return instance
      .update(fd)
      .then((res: any) => onSuccess?.(res))
      .catch((err: any) => {
        if (onError) return onError(err);
        return context?.handleError?.(err);
      });
  };
  return <SupplierOrderForm {...rest} onSubmit={handleSubmit} />;
};

//#region ==================================== SUPPLIER ORDER FORM

export const SupplierOrderForm = (props: FormProps) => <Form {...props} />;

SupplierOrderForm.NameField = (props: TextFieldProps) => {
  const { label = 'Name', text = 'Write a name to help remember your order.', error, ...rest } = props;
  return (
    <Form.Field label={label} text={text} error={error}>
      <Form.Text placeholder="Nom" maxLength={99} {...rest} name="name" />
    </Form.Field>
  );
};

SupplierOrderForm.OrderIDField = (props: TextFieldProps) => {
  const { label = 'Order ID', text, error, ...rest } = props;
  return (
    <Form.Field label={label} text={text} error={error}>
      <Form.Text placeholder="Order ID" maxLength={99} {...rest} name="order_id" />
    </Form.Field>
  );
};

SupplierOrderForm.IsPaidField = (props: CheckboxFieldProps) => {
  const { label = 'Payé', text, error, ...rest } = props;
  return (
    <Form.Field label={label} text={text} error={error} checkbox>
      <Form.Checkbox {...rest} name="is_paid" />
    </Form.Field>
  );
};

SupplierOrderForm.TotalCostField = (props: TextFieldProps) => {
  const { label = 'Coût total', text, error, ...rest } = props;
  return (
    <Form.Field label={label} text={text} error={error}>
      <Form.Text min="0" step="0.01" {...rest} required type="number" name="total_cost" />
    </Form.Field>
  );
};

SupplierOrderForm.CurrencyField = (props: SelectFieldProps & { currencies: TextChoicesType }) => {
  const { label, text = 'Select the supplier currency.', error, currencies, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Select {...rest} required name="currency" nullValue={{ label: 'Sélectionnez une monnaie ...' }}>
        {currencies?.map((curr) => {
          return <Form.Option {...curr} key={curr.value} />;
        })}
      </Form.Select>
    </Form.Field>
  );
};

SupplierOrderForm.SupplierField = (props: SelectFieldProps & { suppliers: TextChoicesType }) => {
  const { label, text = 'Select the supplier.', error, suppliers, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Select {...rest} required name="supplier" nullValue={{ label: 'Sélectionnez le supplier ...' }}>
        {suppliers?.map((supplier) => {
          return <Form.Option {...supplier} key={supplier.value} />;
        })}
      </Form.Select>
    </Form.Field>
  );
};

//#endregion ==================================== SUPPLIER ORDER FORM

interface ISupplierOrderProductAddFormProps extends IFormCallbackProps {
  orderInstance: ReturnType<typeof SupplierOrder>;
  children?: React.ReactNode;
}

export const SupplierOrderProductAddForm = ({ children, ...props }: ISupplierOrderProductAddFormProps) => {
  const { orderInstance, onSuccess, onError } = props;
  const supplier = orderInstance?.supplier?.toLowerCase();

  const form = useForm({ supplier_name: supplier, url: '' });
  const [isLoading, setLoading] = React.useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const { url } = form.values;

    const handleSuccess = (res: any) => {
      setLoading(false);
      form.setValue('url', '');
      return onSuccess?.(res);
    };

    const handleError = (err: any) => {
      form.setErrors({ ...form.errors, url: 'Something went wrong.' });
      setLoading(false);
      return onError?.(err);
    };

    const urlErrMsg = `Invalid Url: does not match supplier ${orderInstance.supplier}`;

    switch (supplier) {
      case 'fnova':
        if (!url.includes('fashionnova')) return form.setErrors({ url: urlErrMsg });

        setLoading(true);
        return orderInstance.postFnova(url).then(handleSuccess).catch(handleError);
      case 'plt':
        if (!url.includes('prettylittlething')) return form.setErrors({ url: urlErrMsg });

        setLoading(true);
        return orderInstance.postPlt(url).then(handleSuccess).catch(handleError);

      default:
        if (!url.includes(supplier)) return form.setErrors({ url: urlErrMsg });

        setLoading(true);
        return orderInstance.postShein(url).then(handleSuccess).catch(handleError);
    }
  };

  return (
    <Form context={form} onSubmit={handleSubmit} className="supplier-product-add-form">
      <Form.Field error={form.errors.url}>
        <div className="d-flex flex-column flex-md-row align-items-center">
          <Form.Text
            required
            autoFocus
            name="url"
            type="url"
            placeholder={`Add ${supplier} product source url ...`}
            className="me-1"
            disabled={isLoading}
          />

          <div className="mt-1 mt-md-0">
            <Form.Submit value="Add product" className="btn btn-primary-3" disabled={isLoading} />
          </div>
        </div>
      </Form.Field>

      {isLoading && <div className="my-3">Getting product data. Please wait ...</div>}
    </Form>
  );
};

const u = 'https://www.fashionnova.com/products/jessa-denim-mini-dress-white';
const shein404 = 'https://us.shein.com/Clear-Acrylic-Frame-Glasses-p-1792787-cat-2842.html';
const fnovavariant = 'https://www.fashionnova.com/products/kayla-seamless-bandeau-olive?variant=12191869272188';
