import * as React from 'react';

import Form, { useForm } from '@miq/formjs';
import { useRequest } from '@miq/hookjs';
import Staff from '@miq/staffjs';
import { Response } from '@miq/utiljs';
import { useParams } from 'react-router-dom';
import { SupplierOrder } from './models';
import { Loading, ViewSection } from '@miq/componentjs';
import { SupplierOrderForm, SupplierOrderProductAddForm, SupplierOrderUpdateForm } from './forms';
import SupplierProductList from './SupplierProductList';
import { SupplierOrderType } from '../types';
import { SharedDataCtx } from '@miq/contextjs';

const formFields: { [key: string]: any } = {
  name: '',
  order_id: '',
  items_stage: '',
  currency: 'USD',
  supplier: '',
  is_paid: false,
  total_cost: '',
};

const mapDataToValues = (data: any = {}): SupplierOrderType => {
  const values: any = {};
  Object.keys(formFields).forEach((k) => {
    values[k] = data[`${k}`] || formFields[`${k}`];
  });
  return values;
};

export default function OrderUpdateView() {
  const { ordSlug } = useParams();

  const form = useForm<SupplierOrderType>(mapDataToValues());

  const { suppliers, currencies } = React.useContext(SharedDataCtx) as any;

  const { setValues } = form;
  const {
    res,
    loading,
    setLoading,
    setRes: setOrder,
    refresh: refreshOrder,
  } = useRequest(() => SupplierOrder({ slug: ordSlug }).retrieve(), {
    refreshDeps: [ordSlug],
    onSuccess: (res) => setValues(mapDataToValues(res.data)),
  });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  const instance = SupplierOrder(r.data);

  if (!instance.supplier || !instance.currency)
    return (
      <Staff.View title="Modifier une commande" back="/staff/store/orders/">
        <Staff.Section className="d-flex justify-content-center mt-4">
          <SupplierOrderUpdateForm
            context={form}
            instance={instance}
            fields={['name', 'currency', 'supplier']}
            onSuccess={(res) => setOrder(res)}
          >
            <SupplierOrderForm.NameField required label="Nom" error={form.errors.currency} />
            <SupplierOrderForm.SupplierField
              required
              label="Supplier"
              suppliers={suppliers}
              error={form.errors.supplier}
            />
            <SupplierOrderForm.CurrencyField
              required
              label="Supplier Currency"
              currencies={currencies}
              error={form.errors.currency}
            />

            <Form.Submit value="Sauvegarder" className="btn-primary-3" />
          </SupplierOrderUpdateForm>
        </Staff.Section>
      </Staff.View>
    );

  return (
    <Staff.View title={form.values.name} back="/staff/store/orders/">
      <div className="d-flex flex-column flex-xl-row">
        <div className="min-w-35">
          <Staff.Section
            border
            className="bg-white mb-1"
            title={`Order Details${!loading && ` (${instance.products?.length})`}`}
            text={`Supplier: ${instance.supplier} | Currency: ${instance.currency}`}
          >
            <div className="d-grid grid-md-5 gap-2">
              <div className="span-md-3">
                <SupplierOrderUpdateForm
                  {...{ instance, context: form, fields: ['is_paid', 'order_id', 'total_cost'] }}
                  onSuccess={(res) => setOrder(res)}
                >
                  <ViewSection border footer={<Form.Submit value="Sauvegarder" className="btn-primary-3" />}>
                    <SupplierOrderForm.IsPaidField error={form.errors.is_paid} />
                    {form.values.is_paid && (
                      <>
                        <SupplierOrderForm.OrderIDField required={form.values.is_paid} error={form.errors.order_id} />
                        <SupplierOrderForm.TotalCostField
                          required={form.values.is_paid}
                          error={form.errors.total_cost}
                        />
                      </>
                    )}
                  </ViewSection>
                </SupplierOrderUpdateForm>
              </div>

              <div className="span-md-2">
                <ul className="mb-3">
                  <li>Total cost: {instance.items_cost.amountWithSymbol}</li>
                  <li>Est. revenue: {instance.items_revenue.amountWithSymbol}</li>
                </ul>
              </div>
            </div>
          </Staff.Section>
        </div>

        <div className="miq-container-1024 ms-xl-1">
          <Staff.Section
            header={
              <div className="miq-container-md center bg-white">
                <SupplierOrderProductAddForm
                  orderInstance={instance}
                  onSuccess={(res) => {
                    setLoading();
                    setOrder(res);
                  }}
                />
              </div>
            }
          >
            {loading ? (
              <Loading icon />
            ) : (
              <SupplierProductList
                orderInstance={instance}
                categories={r?.data?.categories}
                refreshOrder={refreshOrder}
              />
            )}
          </Staff.Section>
        </div>
      </div>
    </Staff.View>
  );
}
